import React, { Component } from 'react'
import { connect } from 'react-redux';
import styles from './ContactForm.module.css';
import { v4 as uuidv4 } from 'uuid';
import { getItems, addContact } from '../../redux/phonebooks';
// import * as phonebooksOperations from '../../redux/phonebooks/phonebooks-operations';
// import * as phonebookSelectors from '../../redux/phonebooks/phonebooks-selectors';

class ContactForm extends Component {
    inputId = uuidv4();
    state = {
        name: '',
        number: '',

    };
    handleInputChange = evt => {
        console.log(evt.currentTarget.value);
        const { name, value } = evt.currentTarget;
        this.setState({ [name]: value });

    };
    handleSubmit = evt => {
        evt.preventDefault();
        const findContact = this.props.contacts.find(contact => contact.name === this.state.name && contact.number === this.state.number);

        if (findContact) {
            alert(`${this.state.name} is already in contacts!`);
            this.reset();
            return;
        }
        this.props.addContact(this.state.name, this.state.number);
        this.reset();
    }
    reset = () => {
        this.setState({ name: '', number: '' });
    };

    render() {
        return (

            <div className={styles.container}>
                <form onSubmit={this.handleSubmit} className={styles.form}>
                    <label htmlFor={this.inputId} className={styles.label}>

                        Name:

                        <input
                            className="Container-form__input"
                            type="text"
                            name="name"
                            pattern="^[a-zA-ZÐ°-ÑÐ-Ð¯]+(([' -][a-zA-ZÐ°-ÑÐ-Ð¯ ])?[a-zA-ZÐ°-ÑÐ-Ð¯]*)*$"
                            title="ÐÐ¼Ñ Ð¼Ð¾Ð¶ÐµÑ ÑÐ¾ÑÑÐ¾ÑÑÑ ÑÐ¾Ð»ÑÐºÐ¾ Ð¸Ð· Ð±ÑÐºÐ², Ð°Ð¿Ð¾ÑÑÑÐ¾ÑÐ°, ÑÐ¸ÑÐµ Ð¸ Ð¿ÑÐ¾Ð±ÐµÐ»Ð¾Ð². ÐÐ°Ð¿ÑÐ¸Ð¼ÐµÑ Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan Ð¸ Ñ. Ð¿."
                            required
                            value={this.state.name}
                            onChange={this.handleInputChange}
                            id={this.inputId}
                        />
                    </label>
                    <label className={styles.label}>
                        Number:
                        <input
                            type="tel"
                            name="number"
                            pattern="\+?\d{1,4}?[-.\s]?\(?\d{1,3}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}"
                            title="ÐÐ¾Ð¼ÐµÑ ÑÐµÐ»ÐµÑÐ¾Ð½Ð° Ð´Ð¾Ð»Ð¶ÐµÐ½ ÑÐ¾ÑÑÐ¾ÑÑÑ ÑÐ¸ÑÑ Ð¸ Ð¼Ð¾Ð¶ÐµÑ ÑÐ¾Ð´ÐµÑÐ¶Ð°ÑÑ Ð¿ÑÐ¾Ð±ÐµÐ»Ñ, ÑÐ¸ÑÐµ, ÐºÑÑÐ³Ð»ÑÐµ ÑÐºÐ¾Ð±ÐºÐ¸ Ð¸ Ð¼Ð¾Ð¶ÐµÑ Ð½Ð°ÑÐ¸Ð½Ð°ÑÑÑÑ Ñ +"
                            value={this.state.number}
                            onChange={this.handleInputChange}
                            required
                        />
                    </label>

                    <button type="submit" className="Container-form__btn">Add Contact</button>
                </form>
            </div>
        )
    };
};


const mapStateToProps = state => ({
    contacts: getItems(state),
});
const mapDispatchToProps = dispatch => ({
    addContact: (name, number) => dispatch(addContact(name, number)),

})

export default connect(mapStateToProps, mapDispatchToProps)(ContactForm);

